.book-seat a:hover {
  color: orange !important;
  text-decoration: none;
}
.book-seat .MuiTabs-flexContainer {
  display: inline-block !important;
}
.book-seat .MuiAppBar-colorPrimary {
  background-color: #fff;
  color: black;
}
.booking-seat .style-char button {
  font-size: 11px !important;
  margin: 3px 5px 3px 5px;
  width: 44px;
}
.booking-seat .style-char {
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  border-radius: 5px;
}
.detail-info-book {
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  border-radius: 5px;
}
.booking-seat .book-ticket {
  width: 100%;
}
.booking-seat .hidden-seat {
  overflow: hidden;
  overflow: auto;
  height: 500px;
  text-align: center;
}

.booking-seat .info-cost-seat {
  min-height: 100px;
  height: 100px;
  width: 100%;
  overflow: hidden;
}
.info-cost-seat span {
  display: inline-block;
}

.info-cost-seat:hover {
  overflow-y: auto;
}
.info-cost-seat::-webkit-scrollbar {
  width: 4px;
  background-color: #e8e3e3;
}

.booking-seat .type-seat button {
  width: 35px;
  height: 25px;
}
.width-870 {
  width: 870px;
}
.timelimit-bookseat {
  color: #f98430;
}
.number-seat {
  color: #f98430;
}
/* resbonsive*/
@media screen and (max-width: 1310px) {
  .booking-seat .hidden-seat {
    display: block !important;
  }
}
